import React from 'react';
import ReactDOM from 'react-dom'
import {withRouter} from 'react-router-dom';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid'
import Button from '@material-ui/core/Button'
import IconButton from '@material-ui/core/IconButton'
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import CardActionArea from '@material-ui/core/CardActionArea'
import Card from '@material-ui/core/Card'
import CardActions from '@material-ui/core/CardActions'
import Paper from '@material-ui/core/Paper'
import Hidden from '@material-ui/core/Hidden'
import Drawer from '@material-ui/core/Drawer';
import Divider from '@material-ui/core/Divider';
import {BarChart} from 'react-easy-chart'
import LocationImage from '../components/LocationImage.jsx'
import ProbGraph from '../components/ProbGraph.jsx'

import ScrollspyNav from "react-scrollspy-nav";

import '../App.css';

import {large_threshold} from '../files/constants.js'
import {locationNumberToName} from '../files/constants.js'

//Image imports
import aneurysm_img from '../images/aneurysm.png'
import aneurysm_flow_img from '../images/aneurysm_flow.svg'
import location_img from '../images/aneurysm_location.png'
import coiling_img from '../images/coiling.svg'
import donothing_img from '../images/donothing.png'
import angio_img from '../images/angio.jpg'

import pron_file from '../files/ca_pronounced.mp3'

import VolumeUpIcon from '@material-ui/icons/VolumeUp'
import ChevronRight from '@material-ui/icons/ChevronRight'
import ChevronLeft from '@material-ui/icons/ChevronLeft'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore'
import Close from '@material-ui/icons/Close'

//
var pron = new Audio(pron_file)

class Home extends React.Component {

  updatePatientWithParams() {
    if (typeof this.props.match.params.age != 'undefined') {
      var newPatient = this.props.patient;
      newPatient.age = this.props.match.params.age;
      newPatient.lesionDiameter = this.props.match.params.size;
      newPatient.lesionLocation = this.props.match.params.location;
      newPatient.ethnicity = this.props.match.params.ethnicity;
      newPatient.htn = this.props.match.params.htn === 'true'
      newPatient.hxSAH = this.props.match.params.sah === 'true'
      this.props.updatePatient(newPatient);
    }
  }
  constructor(props) {

    super(props);
    this.state = {
      isLarge: window.innerWidth > large_threshold,
      location: this.props.match.params.location != 'undefined'
        ? locationNumberToName[this.props.match.params.location]
        : '',
      drawer: false,
      graphProb: 'ucas'
    }
    window.addEventListener('resize', () => {
      this.setState({
        isLarge: window.innerWidth > large_threshold
      });
    }, false);

    this.updatePatientWithParams();

  }

  componentDidUpdate() {
    var hash = this.props.location.hash.replace('#', '');
    if (hash) {
      let node = ReactDOM.findDOMNode(this.refs[hash]);
      if (node) {
        node.scrollIntoView()
        window.scrollBy(0, -100);
      }
    }
  }

  pronounce() {
    pron.play();
  }

  locationStatement() {
    var div = null;
    if (typeof this.state.location != 'undefined') {
      div = " Your aneurysm is located on the " + locationNumberToName[this.props.match.params.location] + ".";
    }
    return div;
  }
  urlWithParams(base) {
    var params = "/";
    params = params + this.props.patient.age + '/' + this.props.patient.lesionDiameter + '/' + this.props.patient.lesionLocation + '/' + this.props.patient.ethnicity + '/' + this.props.patient.htn + '/' + this.props.patient.sah;

    var url = '#/' + base + params;
    return url;
  }

  toggleDrawer() {
    this.setState({
      drawer: !this.state.drawer
    });
  };

  drawer() {
    return (<div className={'landing-nav norm_padding'}>
      <Typography variant={'h6'}>
        Quick Navigation:</Typography>
      <div className={'landing-nav'}>
        <ScrollspyNav scrollTargetIds={["pathology", "location", "risk", "options"]} activeNavClass="is-active" router={'Router'}>
          <a href="#summary">Aneurysm Summary</a>
          <a href="#pathology">What is an aneurysm?</a>
          <a href="#location">Where is my aneurysm?</a>
        </ScrollspyNav>
      </div>
    </div>)
  }

  setGraphProb(study) {
    this.setState({graphProb: study});
  }

  render() {
    var size_class = this.state.isLarge
      ? 'large'
      : '';
    return (<div>
      <Grid container={true} direction={'row'} alignContent={'stretch'}>
        <Grid container={true} item={true} justify={'flex-start'} alignContent={'flex-start'} className={'first-block'} md={9} xs={12}>
          <Paper>
            <Grid item={true} container={true} xs={12}>
              <Typography variant='h3' className={'landing-title'}>I have an aneurysm.</Typography>
            </Grid>
            <Grid item={true} container={true} xs={12}>
              <Typography variant='h4' gutterBottom={true} className={'landing-subtitle'}>What do I do now?</Typography>
            </Grid>
            <Grid item={true} xs={12}>
              <Typography variant='body2' gutterBottom={true} className={'limit_md landing-welcome'}>Being diagnosed with an aneurysm can be overwhelming. This guide has the essential information to help you understand aneurysms and the basic ideas of how they are treated. This document does not contain everything there is to know, focusing instead on ensuring clear understanding of the basics.</Typography>
            </Grid>
            <Divider/>
            <Grid className={'content-block summary'} id={'summary'} item={true} xs={12} container={true} direction={this.state.isLarge
                ? 'row-reverse'
                : 'column'}>
              <Grid item={true} xs={12} sm={6} container={true} alignItems={'center'}>
                <LocationImage variant={'callout'} location={this.props.patient.lesionLocation}/>
              </Grid>
              <Grid item={true} xs={12} sm={6}>
                <div className={size_class + ' summary-block-text'}>
                  <p className={'supertitle'}>BASICS:</p>
                  <Typography variant='h5' gutterBottom={true}>Need to know: aneurysms</Typography>
                  <Typography variant='body1' paragraph={true}>Cerebral aneurysms
                    <VolumeUpIcon className={'inline-icon'} color={'primary'} onClick={() => this.pronounce()}/>
                    are a problem with blood vessels in the brain. For some people, they can cause problems, but for most people, they will not.
                  </Typography>
                  <Typography variant='body1' paragraph={true}>2 kinds of problems caused by aneurysms:</Typography>
                  <ul>
                    <li className={'MuiTypography-body1-50 '}>
                      <b>Neurological problems</b>, such as headaches and other issues like blurred vision, weakness, numbness, or difficulty speaking by growing big and pressing on the brain.
                    </li>
                    <li className={'MuiTypography-body1-50 '}>
                      <b>Rupture</b>, causing a bleed in the brain that can be deadly or cause permanent brain damage.
                    </li>
                  </ul>

                  <Typography variant='body1' paragraph={true}>
                    <b>Aneurysms are treatable</b>, and your provider will work with you to determine the best plan for you.
                  </Typography>

                </div>
              </Grid>
            </Grid>
            <Divider/>
            <Grid id={'pathology'} item={true} container={true} xs={12} alignItems={'center'} className={'content-block '} direction={'row'}>
              <Grid item={true} xs={12} sm={6} className={'norm_padding'}>
                <img src={aneurysm_flow_img} className={'limit_md grid_img'}/>
              </Grid>
              <Grid item={true} xs={12} sm={6} className='landing_text_block'>

                <Typography variant='h5' gutterBottom={true}>What is an aneurysm?</Typography>
                <Typography variant={'body1'} paragraph={true}>An aneurysm is an abnormal growth from the wall of the blood vessels in the brain. About 3% of people have an aneurysm. Most will never have a problem caused by their anuerysm.
                </Typography>

                <Typography variant='title' gutterBottom={true}>How do aneurysms cause problems?</Typography>
                <Typography variant={'body1'} paragraph={true}>Some anerysms will grow large and press on the brain causing headaches and other neurological issues that people may experience. The wall of the aneurysm may also be weak compared the wall of normal blood vessels. For this reason, they can rupture and leak blood into the brain.
                </Typography>
              </Grid>
            </Grid>
            <Divider/>
            <Grid id={'location'} item={true} container={true} xs={12} alignItems={'center'} className={'content-block '} direction={'row'}>
              <Grid item={true} xs={12} sm={6} className='landing_text_block'>
                <Typography variant='h5' gutterBottom={true}>Where are aneurysms located?</Typography>
                <Typography variant={'body1'} paragraph={true}>Brain aneurysms are named for the arteries on which they are found. Aneurysm location is important because it can predict how likely your aneurysm is to rupture and determine what kind of treatment is best for you.
                </Typography>
                <Typography variant={'body1'} paragraph={true}>
                  <b>{this.locationStatement()}</b>
                </Typography>

              </Grid>
              <Grid item={true} xs={12} sm={6} className={'norm_padding'}>
                <LocationImage variant={'locate'} location={this.props.patient.lesionLocation}/>
              </Grid>
            </Grid>
            <Grid item={true} container={true} xs={12} direction={'row'} justify={'flex-end'} className={'norm_padding'}>
              <Button variant={'contained'} color={'primary'} href={this.urlWithParams('decision')}>Learn about Options
                <ChevronRight/></Button>
            </Grid>

          </Paper>
        </Grid>
        <Hidden smDown={true}>
          <Grid item={true} sm={3}>
            {this.drawer()}
          </Grid>
        </Hidden>
        <Drawer anchor={'right'} open={this.state.drawer} onClose={() => this.toggleDrawer()}>
          <div tabIndex={0} role="button" onClick={() => this.toggleDrawer()} onKeyDown={() => this.toggleDrawer()}>
            <IconButton onClick={() => this.toggleDrawer()}><Close/></IconButton>
            {this.drawer()}
          </div>
        </Drawer>

      </Grid>
      <Hidden mdUp={true}>
        <IconButton onClick={() => this.toggleDrawer()} className={'drawer-button'}><ChevronLeft/></IconButton>
        </Hidden></div >)
  }
}

export default Home;
